import React, { useState } from 'react';
import type { Meta, StoryFn } from '@storybook/react';

import { Drawer, DrawerSlide } from '@tiger-analytics/react/overlay';
import { Button, ButtonVariant } from '@tiger-analytics/react/button';
import { GridColumn, GridContainer } from '@tiger-analytics/react/grid';

import { TextInput, TextArea } from '@tiger-analytics/react/formFields';

import styled from 'styled-components';

import { DescriptionBlock, ImportBlock } from '../../utils';

import { DrawerSlide2 } from './DrawerSlide2';

export default {
  title: 'Overlay/Drawer/Drawer',
  component: Drawer,
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    visible: {
      description: 'Controls the visibility of the drawer',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: false,
    },
    title: {
      description: 'Mandatory - title to be displayed on the drawer',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    idToFocusOnClose: {
      description: 'Accessibility - id of element to focus on close of Modal',
      table: {
        defaultValue: { summary: '' },
      },
      control: false,
    },
    onClose: {
      description: 'Task to perform on close of the drawer.',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
    onSubmit: {
      description:
        'Task to perform on submit of the drawer. If the current DrawerSlide has validate property then its outcome (true/false) will decide if onSubmit will be executed or not.',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
  },
} as Meta;

const description = (
  <div>
    The Drawer component is used to display sliding drawers, typically from the right side of the
    screen. It is ideal for scenarios where you need to present additional content or actions
    without interrupting the main flow. It can support both single-step and multi-step content
    (automatically decided based on the number of DrawerSlide children).
    <br />
    <br />
    <b>Note: </b>within any DrawerSlide component, useDrawerController hook can be used to
    programmatically navigate to specific slide:
    <br />
    <pre>{`const {setActiveSlide} = useDrawerController();`}</pre>
  </div>
);

const ErrorContainer = styled.div`
  font-size: 0.8rem;
  color: red;
`;

const Template: StoryFn = (args) => {
  const [isSingleStepDrawrrVisible, setIsSingleStepDrawrrVisible] = useState(false);
  const [isMultiStepDrawrrVisible, setIsMultiStepDrawrrVisible] = useState(false);
  const [taskName, setTaskName] = useState('');
  const [taskDescription, setTaskDescription] = useState('');
  const [error, setError] = useState('');
  const [taskName2, setTaskName2] = useState('');
  const [taskDescription2, setTaskDescription2] = useState('');
  const [error2, setError2] = useState('');

  const drawerCloseHandler = (drawerId: string, drawerOpenerId: string) => {
    const drawerActivateButton = document.getElementById(drawerOpenerId);

    if (drawerActivateButton) {
      drawerActivateButton.focus();
    }
    if (drawerId === `${args.id}-single`) {
      setIsSingleStepDrawrrVisible(false);
      setTaskName('');
      setTaskDescription('');
      setError('');
    } else {
      setIsMultiStepDrawrrVisible(false);
      setTaskName2('');
      setTaskDescription2('');
      setError2('');
    }
  };

  const taskNameChangeHandler = (value: string) => {
    setTaskName(value);
    setError('');
  };

  const taskDescriptionChangeHandler = (value: string) => {
    setTaskDescription(value);
    setError('');
  };

  const taskNameChangeHandler2 = (value: string) => {
    setTaskName2(value);
    setError2('');
  };

  const taskDescriptionChangeHandler2 = (value: string) => {
    setTaskDescription2(value);
    setError2('');
  };

  const slide1Validator = (): boolean => {
    if (!taskName || !taskDescription) {
      setError('Both name and description are required.');
      return false;
    } else {
      return true;
    }
  };

  const slide1Validator2 = (): boolean => {
    if (!taskName2 || !taskDescription2) {
      setError2('Both name and description are required.');
      return false;
    } else {
      return true;
    }
  };

  return (
    <GridContainer showSideMargin={false}>
      <ImportBlock
        importStatement={`import { Drawer, DrawerSlide, useDrawerController } from '@tiger-analytics/react/overlay';`}
      />
      <GridColumn style={{ gap: '10px' }}>
        <Button
          id="activate-drawer-single"
          onClick={() => setIsSingleStepDrawrrVisible(true)}
          variant={ButtonVariant.Secondary}>
          Show single step drawer
        </Button>
        <Button
          id="activate-drawer-multi"
          onClick={() => setIsMultiStepDrawrrVisible(true)}
          variant={ButtonVariant.Secondary}>
          Show Multi step drawer
        </Button>
        <Drawer
          id={`${args.id}-single`}
          title="Drawer Single-Step"
          visible={isSingleStepDrawrrVisible}
          onClose={() => drawerCloseHandler(`${args.id}-single`, 'activate-drawer-single')}
          onSubmit={() => drawerCloseHandler(`${args.id}-single`, 'activate-drawer-single')}>
          <DrawerSlide id="slide-1" title="Slide 1" validate={slide1Validator}>
            <TextInput
              id="create-task-name"
              label="Name"
              value={taskName}
              onChangeHandler={taskNameChangeHandler}
            />
            <TextArea
              id="create-task-description"
              label="Description"
              value={taskDescription}
              onChangeHandler={taskDescriptionChangeHandler}
              required
            />
            <ErrorContainer>{error}</ErrorContainer>
          </DrawerSlide>
        </Drawer>
        <Drawer
          id={`${args.id}-multi`}
          title="Drawer Multi-Step"
          visible={isMultiStepDrawrrVisible}
          onClose={() => drawerCloseHandler(`${args.id}-multi`, 'activate-drawer-multi')}
          onSubmit={() => drawerCloseHandler(`${args.id}-multi`, 'activate-drawer-multi')}>
          <DrawerSlide id="slide-2" title="Slide 1" validate={slide1Validator2}>
            <TextInput
              id="create-task-name-2"
              label="Name"
              value={taskName2}
              onChangeHandler={taskNameChangeHandler2}
            />
            <TextArea
              id="create-task-description-2"
              label="Description"
              value={taskDescription2}
              onChangeHandler={taskDescriptionChangeHandler2}
              required
            />
            <ErrorContainer>{error2}</ErrorContainer>
          </DrawerSlide>
          <DrawerSlide2 />
          <DrawerSlide id="slide-3" title="Slide 3">
            Slide 3 content
          </DrawerSlide>
        </Drawer>
      </GridColumn>

      <GridColumn>
        <div className="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      </GridColumn>

      <GridColumn>
        <DescriptionBlock description={description} />
      </GridColumn>
    </GridContainer>
  );
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-drawer',
  visible: false,
};
